___CSS_LOADER_EXPORT___.push([module.id, `.creator-container {
  padding: 0 24px;
  max-width: 1440px;
  margin: 0 auto;
}

.creator__top {
  margin-top: 35px;
  display: flex;
  justify-content: space-between;
}
.creator__video-play-tabs {
  display: flex;
}
.creator__tab {
  cursor: pointer;
  color: #bbb;
  font-size: 24px;
  font-weight: 500;
  padding: 14px 24px;
}
.creator__tab.active {
  color: #fff;
  border-radius: 8px;
  background: var(--9, #222);
}
.creator__add-video-btn {
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  padding: 10px 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.creator__add-video-svg {
  margin-right: 10px;
}
.creator__types {
  display: flex;
  margin-top: 40px;
  border-bottom: 1px solid #222;
}
.creator__type {
  cursor: pointer;
  padding: 8px 24px;
  font-size: 16px;
  color: #777;
  font-weight: 600;
}
.creator__type.active {
  border-bottom: 1px solid #ad7955;
  color: #fff;
}
.creator__videos {
  display: flex;
  flex-wrap: wrap;
  gap: 25px 21px;
  margin-top: 22px;
}

.playlist__videos {
  display: flex;
  flex-wrap: wrap;
  gap: 18px;
}

@media screen and (max-width: 767px) {
  .creator__types {
    justify-content: center;
  }
  .creator__videos {
    justify-content: center;
  }
  .playlist__videos {
    justify-content: center;
  }
  .creator {
    padding: 0 16px;
  }
  .creator__top {
    margin-top: 29px;
  }
  .creator__tab {
    font-size: 16px;
    padding: 10px 15px;
  }
  .creator__add-video-btn {
    position: fixed;
    bottom: 25px;
    z-index: 999;
    right: 25px;
  }
  .creator__types {
    margin-top: 15px;
    width: 100%;
    justify-content: space-between;
    padding: 0 14px;
  }
